import * as React from "react"
import { graphql, useStaticQuery, type HeadFC, type PageProps } from "gatsby"
import Section from "../components/Section"
import MDXRenderer from "../components/MDXRenderer"
import { StaticImage } from "gatsby-plugin-image"

const AboutPage: React.FC<PageProps> = () => {
  const data = useStaticQuery(graphql`
  query About {
    allFile(filter: {sourceInstanceName: {eq: "about"}}) {
      nodes {
        name
        childMdx {
          body
        }
      }
    }
  }
  `)
  return (
    <Section.List>
      <Section.Item
        title="Why Side Trails?"
        subtitle={[<p>"Logic will take you from A to B. Imagination will take you everywhere"</p>, <p>Albert Einstein</p>]}
      >
        <div className="text-base 3xl:text-lg space-y-4 text-justify">
          <div className="float-right ml-4 mb-4 bg-zinc-100 rounded-xl">
            <StaticImage 
              src="../images/logo_full.png" 
              alt=""
              width={224} height={224}
              backgroundColor="transparent"
              transformOptions={{fit: "contain"}}
            />
          </div>
          <MDXRenderer>
            {data.allFile.nodes.find((node: { name: string }) => node.name === "why-side-trails")?.childMdx.body}
          </MDXRenderer>
        </div>
      </Section.Item>
      <Section.Item title="Me">
        <div className="text-base 3xl:text-lg space-y-4 text-justify">
          {/* <div className="float-left mr-4 mb-4">
            <StaticImage
              src="../images/headshot.jpeg" 
              alt="" 
              width={256} height={256} 
              backgroundColor="transparent" 
              transformOptions={{fit: "contain"}} 
              className="rounded-xl"
            />
          </div> */}
        </div>
      </Section.Item>
      <Section.Item title="Values">
      </Section.Item>
    </Section.List>
  )
}

export default AboutPage

export const Head: HeadFC = () => <title>About - Side Trails Software Development</title>
